.overlay {
    position: fixed;
    z-index: 3;
    left: 0;
    top: 0;
    display: flex;
    visibility: hidden;
    align-items: center;
    flex-flow: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    transition:
      opacity 0.5s,
      visibility 0.5s var(--animation-cubic);
    opacity: 0;
    background-color: rgb(0 0 0/0.5);
}

.overlay_is_opened {
  transition: transform 0.7s;
visibility: visible;
  transform: translateX(0);
  opacity: 1;
}
